html[data-theme="dark"] {
    --header-background-color: #191A2A;
    --nav-link-color:#BFC0C2;
    --footer-background-color:#191A2A;
    --footer-text-color:#A39593;
}

html,
html[data-theme="light"] {
    --header-background-color: rgb(255, 255, 255);
    --nav-link-color: #111111;
    --footer-background-color:#E6E6E6;
    --footer-text-color:#403B39;
}

@media (prefers-color-scheme: dark) {
     /* Dark Mode */
    html,
    html[data-theme="dark"] {
        --header-background-color: #191A2A;
        --nav-link-color: #BFC0C2;
        --footer-background-color:#191A2A;
        --footer-text-color:#A39593;
    }

    /* Light Mode */
    html[data-theme="light"] {
        --header-background-color: rgb(255, 255, 255);
        --nav-link-color: #111111;
        --footer-background-color:#E6E6E6;
        --footer-text-color:#403B39;
    }
}




/* Navigation Header */
#topNav{
    padding-left: 100px;
    padding-right: 100px;
    background-color: var(--header-background-color) !important;
    box-shadow: #000 0px 0px 8px;
    font-family: "Source Sans Pro", Helvetica, Arial, sans-serif;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 0.8px;
}


#navbar-hamburger, #navbar-search{
    color: var(--bold-color) !important;
}

.navbar-dark .navbar-brand {
    color: #ff7601 !important;
}
.navbar-dark .navbar-nav .nav-link {
    color: var(--nav-link-color) !important;
}
.navbar-dark .navbar-toggler {
    border-color:transparent !important;
}

.navbar-brand-img{
    width: auto;
    height: 50px;
}


/* Navigation Footer */
footer {
    width: 100%;
    height: 80px;
    text-align: center;
    padding: 20px;
    background-color: var(--footer-background-color);
    color: var(--footer-text-color);
    border-top: 1px solid rgba(0, 0, 0, .05) !important;
    //font-size: 0.8rem;
    //margin-top: 50px;
    //margin-bottom: 62px;
    position: relative;
}


/* Responsive Media Queries */

 /*
    ##Device = Low Resolution Tablets, Mobiles (Landscape)
    ##Screen = B/w 320px to 480px
  */

@media (min-width: 320px) and (max-width: 480px) {
    header {
        height: auto;
        text-align: center;
        background-color: var(--header-background-color);
        box-shadow:0px;
        height: auto;
        width: 100%;
        padding: 1px;

           #author-img {
               margin-left: auto;
               margin-right: auto;
               width: 100%;
           }

           #author_details {
               text-align: center;
           }
    }

    #topNav {
        padding-left:  0px;
        padding-right: 0px;
    }

    .navbar-brand {
        display: inline-block;
        padding-top: .3125rem;
        padding-bottom: .3125rem;
        margin-right: 1rem;
        font-size: 1.25rem;
        line-height: inherit;
        white-space: nowrap;

    }

    .footer {
        margin-top: 0px;
        margin-bottom: 0px;
    }
}

